<template>
    <div>
        <div class="head">
            <img src="static/doctor.jpg" class="doctorImg">
            <div class="doctorInfo">
                <div>
                    <span>朱明清</span>
                    <span class="doctorPosition">教授</span>
                </div>
                <div>
                    <span class="doctorHonour">国际著名针灸中医大师</span>
                </div> 
            </div>
        </div>
        <div class="section">
            <expert-section-head :title="'获得荣誉'"></expert-section-head>
            <pre>1987年——11月14日在北京首届世界针联成立的暨学术交流大会上，以头皮针现场释放治疗急性中风偏瘫病人，使其当场站立走路，从此蜚声海内外。
1989年——入选《针林百家》，黑龙江中医学院
1989年——受台湾红十字会和中国医药学院邀请，以大陆杰出人士身份作为海峡两岸40年来首位医学专业人士访问台湾，开辟了两岸四十年中国医学交流的先例。
1989年——台湾中国针灸学会特等荣誉奖
1990年——中医及气功国际研讨会针灸大师奖
1994年——台湾高雄十大卓越发明家提名
1994年——香港港九中医师公会永久荣誉会长
.......
            </pre>
        </div>
        <div class="section">
            <expert-section-head :title="'医学之路'"></expert-section-head>
            <pre>1964年毕业于上海中医学院，分配在贵州中医药研究所工作。1965年参加首批巡回医疗队，并负责省卫生厅教材的编写工作。1966年文化大革命中被打成“反动学术权威”受批判。后历任贵阳中医学院附属医院针灸副主任，浙江丽水地区针麻辨公室主任、地区医院中医针灸科主任，浙江中医学院针推系针灸研究室主任，北京针灸骨伤学院副教授、针灸研究室主任。中国针灸学会头穴研究组副组长。曾发表多部著作和论文。从1988年以来被邀请到世界各地教学、演讲、示范，接受媒体访问，并担任多个针灸学会的学术顾问，在中医界的影响甚大。1911年定居美国，开设全世界第一所神经专科的针灸诊所，并创立“主氏头皮针教育及研究基金会”以服务、培训与研究为宗旨。
            </pre>
        </div>
        <div class="section lastSection">
            <expert-section-head :title="'主氏头皮针特色'"></expert-section-head>
            <pre>1、通过头部特定的穴位和适量的刺激来激发经络本身的功能，达到疏通经络调理血气的作用。
2、依据中国传统医统理论为指引，脏腑经络学为基础，西医颅部解剖及神经学为用，一同发挥。
3、在头部用浅刺、透穴刺治疗全身的病。
4、采独创的抽气发、进气法为运针手法。（各式头皮针皆不采提插发）
5、配合吐纳、导引、引气至病所。
6、不受体位影响，患者可自由活动患部，减少心理障碍，增进疗效。
7、治疗危、急、重、瘫、痛诸症更见效。
            </pre>
        </div>
        <div class="verbBox">
            <div class="verbBtn" @click="goVerb">
                <span>预约</span>
            </div>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">

import expertSectionHead from 'components/expertSectionHead/expertSectionHead'

export default {
  name: 'expertIntroduction',
  data () {
    return {
    }
  },
  methods: {
    goVerb: function () {
      this.$router.push({
        path: `/doctorVerb`
      })
    }
  },
  components: {
    expertSectionHead
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

$img_height_doctor = 80px
.doctorImg
    width $img_height_doctor
    height $img_height_doctor
    border-radius $img_height_doctor/2
    border 2px solid $color-theme-blue
    margin-left 20px

.head
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    height $img_height_doctor + 40
    background-color white

.doctorInfo
    @extend .flex
    @extend .flex-column
    justify-content space-around
    text-align left
    margin-left 20px
    height $img_height_doctor - 30
    span
        font-weight $font-weight-level_6

.doctorPosition
    color $color-theme-blue
    margin-left 10px

.doctorHonour
    background-color $color-theme-blue
    font-size $font-size-level_0
    color white

.section
    background-color white
    margin-top 10px
    pre
        text-align left
        white-space: pre-wrap
        word-wrap: break-word
        margin 10px 25px 0px 25px
        color black
        line-height 19px
        font-size 13px

.lastSection
    padding-bottom 50px

.verbBox
    position fixed
    height 50px
    width 100%
    bottom 0px
    background-color white
    border-top 1px solid rgba(193, 193, 193, 0.5)
    @extend .flex-around

.verbBtn
    background-color $color-theme-blue
    width 70%
    height 40px
    border-radius 3px
    span
        line-height 40px
        color white
        font-weight $font-weight-level_7
</style>



