<template>
  <div class="head">
      <img src="static/u450.png" class="imgLeft">
      <div class="mid"><span class="title">{{title}}</span></div>
      <img src="static/u452.png" class="imgRight">
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'expertSectionHead',
  props: {
    title: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus" scoped>
@import "~common/stylus/variable"
@import "~common/stylus/base"

headHight = 34px
headWidth = 70%
head_margin_left = 13%

.head
    @extend .flex
    @extend .flex-row
    @extend .flex-start
    height headHight + 10

.imgLeft
    height headHight
    margin-left head_margin_left

.imgRight
    height headHight

.mid
    width headWidth
    height headHight - 10
    background-color $color-theme-blue

.title
    line-height 25px
    color white
    font-weight $font-weight-level_7

</style>



